import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {Avatar, Grid, ListItem, ListItemAvatar, ListItemText, Paper, Typography} from "@mui/material";
import {CoineusUSDFormat} from "../utils/currency_format";
import {makeStyles} from "@mui/styles";
import clsx from "clsx";

const useStyles = makeStyles((theme) => ({
    networkChip: {
        borderRadius: "50%",
        marginLeft: 30,
        position: 'absolute',
        top: 42,
        height: 16,
        width: 16,
        display: 'inline-block',
        backgroundSize: 'cover',
        '&.fuse': {
            backgroundImage: `url('https://coineus.app/assets/tokens/fuse/logo.png')`,
        },
        '&.avax': {
            backgroundImage: `url('https://coineus.app/assets/tokens/avax/logo.png')`,
        },
        '&.bnb': {
            backgroundImage: `url('https://coineus.app/assets/tokens/bnb/logo.png')`,
        },
        '&.eth': {
            backgroundImage: `url('https://coineus.app/assets/tokens/eth/logo.png')`,
        }
    },
    negative: {
        color: 'red'
    },
    positive: {
        color: '#01bc01'
    }
}));

export default function PriceScroll() {

    const classes = useStyles();

    const {tokens} = useSelector(state => state.coineus);

    const [list, setList] = useState([]);

    useEffect(() => {

        let tmp = [];
        Object.entries(tokens).map(([chain, tokens]) => {
            Object.entries(tokens).map(([contract_address, token_info]) => {

                const {name, symbol} = token_info;

                switch(symbol){
                    case 'USDC':
                    case 'fUSD':
                    case 'WAVAX':
                    case 'CNSONE':
                    case 'FUD':
                        return[contract_address, token_info];
                    default:
                        break;
                }

                const {priceChange, priceUsd} = token_info.pair

                const t = {
                    name,
                    symbol,
                    chain,
                    contract_address,
                    priceChange: priceChange.h24,
                    priceUsd,
                }

                tmp.push(t);

                return[contract_address, token_info];
            });
        });

        tmp.sort((a, b) => {
            return a.priceChange < b.priceChange ? 1 : -1
        })

        setList(tmp);

    }, [tokens])

    const getColorClass = (value) => {
        if (value === 0) return '';

        return value > 0 ? classes.positive : classes.negative
    }

    return <Grid item xs={12}>
        <div style={{
            margin: '0 auto',
            display: 'grid',
            gridAutoFlow: 'column',
            gap: '1rem',
            height: 'calc(105px + 1rem)',
            overflowY: 'auto',
            overscrollBehaviorX: 'contain',
            scrollSnapType: 'x mandatory'
        }}>
            {
                list.map(token => {
                    return <Paper style={{scrollSnapAlign: 'start', height: 115}} key={token.chain+"_"+token.contract_address}>
                        <ListItem style={{
                            width: 200,
                            maxWidth: 'none',
                            objectFit: 'contain'
                        }}>
                            <ListItemAvatar>
                                <Avatar
                                    src={`https://coineus.app/assets/tokens/${token.chain}/${token.contract_address === 'native' ? '' : token.contract_address + '/'}/logo.png`}/>
                                {token.contract_address !== 'native' && <span className={clsx(classes.networkChip, token.chain)}/>}
                            </ListItemAvatar>
                            <ListItemText
                                primary={token.symbol}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary={CoineusUSDFormat(token.priceUsd)}
                            />
                        </ListItem>
                        <Typography
                            className={getColorClass(token.priceChange)}
                            variant="h5"
                            fontWeight={700}
                            textAlign='right'
                            style={{
                                marginRight: 20
                        }}>{token.priceChange}%</Typography>
                    </Paper>
                })
            }
        </div>

    </Grid>
}