const coineusValidatorAddress = "0x53e2033F86C1a54bEbAE893DC8C9fAe179b3Faa2";
const consensusAddress = "0x3014ca10b91cb3D0AD85fEf7A3Cb95BCAc9c0f79";

function PowerStake(props) {

    const {wallet} = useSelector(state => state.coineus);
    const {open, close, amount, onSubmit, onSuccess, onError} = props;

    const delegate = () => {

        if (!wallet.address) return;

        const web3 = new Web3(window.provider);
        const connectedContract = new web3.eth.Contract(consensus_abi, consensusAddress)

        connectedContract.methods
            .delegate(coineusValidatorAddress)
            .send({
                from: wallet.address,
                value: toWei(amount - .1)
            })
            .once('transactionHash', function (tx) {
                onSubmit();
            })
            .on('error', function (error) {
                onError(error)
            })
            .then(function (receipt) {
                onSuccess()
            })
            .catch((error) => {
                onError(error)
            });
    }

    return <Dialog open={open} fullWidth maxWidth="sm">
        <DialogContent>
            <Typography>Stake <b>{(amount - .1).toLocaleString('en-US', {maximumFractionDigits: 8})}</b> FUSE</Typography>
        </DialogContent>
        <DialogActions>
            <Button onClick={close}>cancel</Button>
            <Button onClick={delegate} variant="contained">Stake</Button>
        </DialogActions>
    </Dialog>
}

function CoineusOne(props) {

    const {wallet} = useSelector(state => state.coineus);

    const {onSuccess} = props;

    const [open, setOpen] = useState(false);
    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");
    const [action, setAction] = useState('stake');

    const deposit = async () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const coineus_contract = new web3.eth.Contract(coineus_abi, "0xc2E299b47398963c618De5b05C6BdeCd4cc64022");
            const contract = new web3.eth.Contract(coineusone_abi, "0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4");


            coineus_contract
                .methods
                .allowance(wallet.address, "0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4")
                .call((err, resp) => {

                    if (err) return;

                    if ((resp / (10**18)) < amount) {
                        coineus_contract
                            .methods
                            .approve("0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4", toWei(amount))
                            .send({from: wallet.address, gasPrice: 10000000000})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                deposit()
                            });
                    } else {
                        deposit()
                    }
                })

            const deposit = () => {
                contract.methods.deposit(toWei(amount))
                    .send({from: wallet.address, gasPrice: 10000000000})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        onSuccess();
                        setAmount('');
                        setPending(false);
                    });
            }
        }
    }

    const withdraw = async () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(coineusone_abi, "0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4");


            contract
                .methods
                .allowance(wallet.address, "0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4")
                .call((err, resp) => {

                    if (err) return;

                    if ((resp / (10**18)) < amount) {
                        contract
                            .methods
                            .approve("0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4", toWei(amount))
                            .send({from: wallet.address, gasPrice: 10000000000})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                withdraw()
                            });
                    } else {
                        withdraw()
                    }
                })

            const withdraw = () => {
                contract.methods.withdraw(toWei(amount))
                    .send({from: wallet.address, gasPrice: 10000000000})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        onSuccess();
                        setAmount('');
                        setPending(false);
                    });
            }
        }
    }

    return <>

        <IconButton
            disabled={
                wallet.chain !== 122 || true
            }
            edge="end"
            onClick={() => setOpen(true)}
            color="primary"
        >
            <SwapVerticalCircleIcon/>
        </IconButton>

        <Dialog open={open} fullWidth maxWidth="sm">
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Paper>
                            <Tabs
                                variant="fullWidth"
                                value={action}
                                onChange={(ev, val) => {
                                    setAction(val)
                                    setAmount('')
                                }}
                            >
                                <Tab id="stake" value="stake" label="STAKE"/>
                                <Tab id="unstake" value="unstake" label="UNSTAKE"/>
                            </Tabs>
                            {
                                action === "stake" && <Box style={{padding: 20}}>
                                    <List>
                                        <ListItem disableGutters disablePadding>
                                            <ListItemText
                                                secondary="Balance"
                                            />
                                            <ListItemText
                                                secondary={`${CoineusCryptoFormat(wallet.balances.fuse["0xc2E299b47398963c618De5b05C6BdeCd4cc64022"])} CNS`}
                                                secondaryTypographyProps={{textAlign: 'right'}}
                                            />
                                        </ListItem>
                                    </List>
                                    <FormControl fullWidth style={{marginBottom: 20}}>
                                        <TextField
                                            autoComplete="off"
                                            type="number"
                                            value={amount}
                                            placeholder="Enter Amount"
                                            onChange={(ev) => {
                                                setAmount(ev.target.value)
                                            }}
                                            InputProps={{
                                                endAdornment: <InputAdornment position="end">
                                                    <Button variant="contained" onClick={() => {
                                                        setAmount(wallet.balances.fuse["0xc2E299b47398963c618De5b05C6BdeCd4cc64022"])
                                                    }
                                                    }>MAX</Button>
                                                </InputAdornment>,
                                            }}
                                        />
                                    </FormControl>
                                    <Button fullWidth variant="contained" disabled={!amount || amount > wallet.balances.fuse["0xc2E299b47398963c618De5b05C6BdeCd4cc64022"]}
                                            onClick={deposit}>Stake</Button>
                                </Box>
                            }

                            {
                                action === "unstake" && <Box style={{padding: 20}}>
                                    <List>
                                        <ListItem disableGutters disablePadding>
                                            <ListItemText
                                                secondary="Balance"
                                            />
                                            <ListItemText
                                                secondary={`${CoineusCryptoFormat(wallet.balances.fuse["0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4"])} CNSONE`}
                                                secondaryTypographyProps={{textAlign: 'right'}}
                                            />
                                        </ListItem>
                                    </List>
                                    <FormControl fullWidth style={{marginBottom: 20}}>
                                        <TextField
                                            autoComplete="off"
                                            type="number"
                                            value={amount}
                                            placeholder="Enter Amount"
                                            onChange={(ev) => {
                                                if (ev.target.value > wallet.balances.fuse["0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4"]) return;
                                                setAmount(ev.target.value)
                                            }}
                                            InputProps={{
                                                endAdornment: <InputAdornment position="end">
                                                    <Button variant="contained" onClick={() => {
                                                        setAmount(wallet.balances.fuse["0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4"])
                                                    }
                                                    }>MAX</Button>
                                                </InputAdornment>,
                                            }}
                                        />
                                    </FormControl>
                                    <Button fullWidth variant="contained"
                                            disabled={!amount || amount > wallet.balances.fuse["0x54259ba97Fa5C6dd265A0e7CE848A6812c0f36a4"]}
                                            onClick={withdraw}>Unstake</Button>
                                </Box>
                            }
                            <Button fullWidth
                                    onClick={()=>setOpen(false)}>Cancel</Button>

                        </Paper>
                    </Grid>

                </Grid>
        </Dialog>

        <CoineusLoading open={pending} label="Transaction Pending"/>
    </>
}