import {
    Avatar,
    Container,
    Grid, List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
} from "@mui/material";
import Web3 from "web3";
import rewards_abi from "../abis/rewards_locker.json";
import coineus_abi from '../abis/coineus.json';

import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat} from "../utils/currency_format";
import {formatWeiToNumber} from "../utils/format";

const coineus_fund_address = "0xb80267eA7fa368374ee4d4Bf10044778232AdEFe";

export default function CoineusPortfolio() {

    const {rpc} = useSelector(state => state.coineus);

    const [data, setData] = useState({});

    useEffect(() => {
        const web3AVAX = new Web3(rpc.avax);
        //rewards
        const coineus_fud_locker = new web3AVAX.eth.Contract(rewards_abi, "0x2873b157ba89812ddf297719b66585a3aa836599");
        //holdings
        const fook_contract = new web3AVAX.eth.Contract(coineus_abi, "0x95923F63dA09B4f7520f7C65a31F318D8228B744");
        const hedge_fund_contract = new web3AVAX.eth.Contract(coineus_abi, "0xe387cF241B6685D405C575ce35db78ed4844B34a");


        const calls = [
            coineus_fud_locker.methods.claimedRewardsTotal().call,
            coineus_fud_locker.methods.lockedTokensAmount().call,

            fook_contract.methods.balanceOf(coineus_fund_address).call,
            fook_contract.methods.getAccountDividendsInfo(coineus_fund_address).call,

            hedge_fund_contract.methods.balanceOf(coineus_fund_address).call,
            hedge_fund_contract.methods.getAccountDividendsInfo(coineus_fund_address).call
        ];

        makeBatchRequest(web3AVAX, calls).then(resp => {
            let [
                coineus_claimed,
                coineus_locked,

                coineus_fund_fook_balance,
                coineus_fund_fook_dividends,

                coineus_fund_hedge_fund_balance,
                coineus_fund_hedge_fund_dividends
            ] = resp;

            setData({
                coineus_claimed,
                coineus_locked,

                coineus_fund_fook_balance,
                coineus_fund_fook_dividends: coineus_fund_fook_dividends[4]/(10**6),

                coineus_fund_hedge_fund_balance,
                coineus_fund_hedge_fund_dividends: coineus_fund_hedge_fund_dividends[4]/(10**18)
            })

        });

    }, []);

    return <Container maxWidth="sm">
        <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar src="https://coineus.app/assets/tokens/avax/0x80363cc2CAa2545554d155031e47049ee2F98f33/logo.png" />
                            </ListItemAvatar>
                            <ListItemText
                                primary="FUD Token"
                                primaryTypographyProps={{fontWeight:700}}
                                secondary="Rewards Locker"
                            />
                        </ListItem>
                        <ListItem >
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_locked)) + " FUD"}
                                primaryTypographyProps={{fontWeight:700}}
                                secondary="Locked"
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_claimed)) + " WAVAX"}
                                primaryTypographyProps={{fontWeight:700}}
                                secondary="Earned All-time"
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar src="https://coineus.app/assets/tokens/avax/0x95923F63dA09B4f7520f7C65a31F318D8228B744/logo.png" />
                            </ListItemAvatar>
                            <ListItemText
                                primary="FOOK Token"
                                primaryTypographyProps={{fontWeight:700}}
                                secondary="Fund Holdings"
                            />
                        </ListItem>
                        <ListItem >
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_fund_fook_balance)) + " FOOK"}
                                primaryTypographyProps={{fontWeight:700}}
                                secondary="Balance"
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(data.coineus_fund_fook_dividends) + " USDC"}
                                primaryTypographyProps={{fontWeight:700}}
                                secondary="Earned All-time"
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Paper>
                    <List>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar src="https://coineus.app/assets/tokens/avax/0xe387cF241B6685D405C575ce35db78ed4844B34a/logo.png" />
                            </ListItemAvatar>
                            <ListItemText
                                primary="Hedge Fund Token"
                                primaryTypographyProps={{fontWeight:700}}
                                secondary="Fund Holdings"
                            />
                        </ListItem>
                        <ListItem >
                            <ListItemText
                                primary={CoineusCryptoFormat(formatWeiToNumber(data.coineus_fund_hedge_fund_balance)) + " Hedge Fund"}
                                primaryTypographyProps={{fontWeight:700}}
                                secondary="Balance"
                            />
                            <ListItemText
                                primary={CoineusCryptoFormat(data.coineus_fund_hedge_fund_dividends) + " FOOK"}
                                primaryTypographyProps={{fontWeight:700}}
                                secondary="Earned All-time"
                            />
                        </ListItem>
                    </List>
                </Paper>
            </Grid>

        </Grid>
    </Container>
}