import {Avatar, ListItem, ListItemAvatar, ListItemText, Paper} from "@mui/material";
import {CoineusUSDFormat, CoineusCryptoFormat} from "../utils/currency_format";
import clsx from "clsx";
import {makeStyles} from "@mui/styles";

import TokenInfoDialog from "./TokenInfoDialog";
import {useState} from "react";
import {useSelector} from "react-redux";

const useStyles = makeStyles((theme) => ({
    networkChip: {
        borderRadius: "50%",
        marginLeft: 30,
        position: 'absolute',
        top: 42,
        height: 16,
        width: 16,
        display: 'inline-block',
        backgroundSize: 'cover',
        '&.fuse': {
            backgroundImage: `url('https://coineus.app/assets/tokens/fuse/logo.png')`,
        },
        '&.avax': {
            backgroundImage: `url('https://coineus.app/assets/tokens/avax/logo.png')`,
        },
        '&.bnb': {
            backgroundImage: `url('https://coineus.app/assets/tokens/bnb/logo.png')`,
        },
        '&.eth': {
            backgroundImage: `url('https://coineus.app/assets/tokens/eth/logo.png')`,
        }
    }
}));

export default function TokenListItem(props) {

    const classes = useStyles();

    const {wallet} = useSelector(state => state.coineus);

    const {contract_address, name, network, price, rewards, rewardsClaim, native, wrap} = props;

    const [open, setOpen] = useState(false);

    if (wallet.balances[network] === undefined) {
        return <></>;
    }

    return <Paper style={{marginTop: 10, marginBottom: 5}}>
        <ListItem secondaryAction={props.secondaryAction}>
            <ListItemAvatar onClick={() => setOpen(true)}>
                <Avatar
                    src={`https://coineus.app/assets/tokens/${network}/${contract_address === 'native' ? '' : contract_address + '/'}logo.png`}/>
                {network && !native && <span className={clsx(classes.networkChip, network)}/>}
            </ListItemAvatar>
            <ListItemText
                primary={name}
                secondary={CoineusUSDFormat(price?.inUSD)}
                primaryTypographyProps={{fontWeight: 700}}
            />
            <ListItemText
                primary={CoineusCryptoFormat(wallet.balances[network][contract_address])}
                secondary={CoineusUSDFormat(price?.inUSD * wallet.balances[network][contract_address])}
                primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                secondaryTypographyProps={{textAlign: 'right'}}
            />
        </ListItem>

        {
            wallet.balances.fuse['0xc2E299b47398963c618De5b05C6BdeCd4cc64022'] >= 100 &&
            rewards && <>
                <ListItem style={{color: '#fbca00'}}>
                    <ListItemText
                        primary='Rewards All-time'
                        secondary='Pending'
                        primaryTypographyProps={{fontWeight: 700}}
                    />
                    <ListItemText
                        primary={`${CoineusCryptoFormat(rewards.allTime)} ${rewards.symbol}`}
                        secondary={`${CoineusCryptoFormat(rewards.pending)} ${rewards.symbol}`}
                        primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                        secondaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
                {rewardsClaim}
            </>
        }


        {/*TODO work on token details*/}
        <TokenInfoDialog
            contract_address={contract_address}
            wrap={wrap}
            network={network}
            open={open}
            close={() => setOpen(false)}/>
    </Paper>
}