import {
    AppBar,
    Avatar,
    Button,
    CssBaseline,
    Drawer,
    Grid,
    IconButton, Link,
    List, ListItem, ListItemText, ListSubheader,
    ThemeProvider,
    Toolbar, Typography
} from "@mui/material";
import theme from './theme';
import logo from './images/logo.svg';
import {
    HashRouter as Router,
    Routes,
    Route,
    Link as RouterLink
} from "react-router-dom";

import HomePage from "./views/HomePage";
import {useDispatch, useSelector} from "react-redux";
import {useEffect, useState} from "react";
import Web3 from "web3";
import useWeb3Modal from "./hooks/useWeb3Modal";
import {
    resetWallet,
    updateWalletAddress,
    updateWalletChain,
    updateWalletProviderType,
} from "./redux/slices/coineusSlice";
import getPairPrices from "./utils/fuse_price";
import Staking from "./views/Staking";
import CoineusSales from "./views/CoineusSales";
import CoineusSaleDetails from "./views/CoineusSaleDetails";
import RewardsLocker from "./views/RewardsLocker";
import CoineusPortfolio from "./views/Portfolio";
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import useDexScreener from "./hooks/useDexScreener";

export default function Coineus() {

    const dispatch = useDispatch();
    const {wallet} = useSelector(state => state.coineus);

    const dex_screener = useDexScreener();

    const [menuOpen, setMenuOpen] = useState(false);
    const [web3Modal, setWeb3Modal] = useState(null);
    const web3ModalProvider = useWeb3Modal();

    useEffect(() => {
        if (window.ethereum) {
            connectProvider(window.ethereum)
        }
        changeWeb3Modal();
    }, []);

    async function changeWeb3Modal() {
        const web3mod = await web3ModalProvider.getWeb3Modal();
        if (web3mod.providerController.cachedProvider) {
            const provider = await web3mod.connect()
            await connectProvider(provider)
        }
        setWeb3Modal(web3mod);
    }

    async function connectWallet() {
        const provider = await web3Modal.connect();
        await connectProvider(provider)
    }

    const connectProvider = async (provider) => {
        window.provider = provider
        const web3 = new Web3(provider);
        subscribeProvider(provider)
        const accounts = await web3.eth.getAccounts();
        if (accounts && accounts.length) {
            //set wallet address
            dispatch(updateWalletAddress(accounts[0]))
        }
        const chainId = parseInt(await web3.eth.net.getId())
        dispatch(updateWalletChain(chainId));
        dispatch(updateWalletProviderType(!!provider.bridge ? 'walletconnect' : 'injected'))
    }

    const subscribeProvider = (provider) => {
        provider.on("close", () => {
            clearWallet();
        });

        provider.on("accountsChanged", async (acc) => {
            if (window.provider) {
                dispatch(updateWalletAddress(acc[0]))
            }
        });

        provider.on("chainChanged", async (networkId) => {
            if (window.provider) {
                const chainId = parseInt(networkId)
                dispatch(updateWalletChain(chainId));
            }
        });
    }

    const clearWallet = async () => {
        const web3 = new Web3(window.provider);
        if (web3 && web3.currentProvider && web3.currentProvider.close) {
            await web3.currentProvider.close();
        }
        dispatch(resetWallet())
        await web3Modal.clearCachedProvider();
        window.provider = null;
    }

    //updates pricing
    useEffect(() => {
        getPairPrices();
        const interval = setInterval(getPairPrices, 60000)
        return () => clearInterval(interval);
    }, [])

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline/>
            <Router>
                <AppBar enableColorOnDark>
                    <Toolbar>
                        <IconButton
                            edge="start"
                            color="inherit"
                            aria-label="open drawer"
                            onClick={() => setMenuOpen(true)}
                        >
                            <MenuIcon/>
                        </IconButton>

                        <RouterLink
                            to={`/`}
                            style={{textDecoration: 'none', display: 'flex'}}
                        >
                            <Avatar src={logo} height={40}/>
                        </RouterLink>
                        <Typography variant="h5">Coineus</Typography>
                        <div style={{flexGrow: 1}}/>
                        {
                            wallet.address ?
                                <Button
                                    variant="outlined"
                                    color="inherit"
                                    size="small"
                                    onClick={() => {
                                        clearWallet();
                                    }}
                                >{getChainLogo(wallet.chain)} 0X... {wallet.address?.substr(-6)}</Button>
                                :
                                <Button
                                    variant="outlined"
                                    color="inherit"
                                    size="small"
                                    onClick={() => connectWallet()}>Connect</Button>
                        }
                    </Toolbar>
                </AppBar>
                <Grid container style={{marginTop: 64}}>
                    <Grid item xs={12}>
                        <Routes>
                            <Route
                                path={`/`}
                                exact={true}
                                element={<HomePage/>}
                            />
                            <Route
                                path={`/staking`}
                                exact={true}
                                element={<Staking/>}
                            />
                            <Route
                                path={`/sale`}
                                exact={true}
                                element={<CoineusSales/>}
                            />
                            <Route
                                path={`/sale/:chain/:sales_contract`}
                                exact={true}
                                element={<CoineusSaleDetails/>}
                            />
                            <Route
                                path={`/rewards`}
                                exact={true}
                                element={<RewardsLocker/>}
                            />
                            <Route
                                path={`/portfolio`}
                                exact={true}
                                element={<CoineusPortfolio/>}
                            />
                        </Routes>
                    </Grid>
                </Grid>

                <Drawer
                    open={menuOpen}
                    onClose={() => setMenuOpen(false)}
                    PaperProps={{style: {minWidth: 240, backgroundColor: '#000', backgroundImage: 'none'}}}

                >
                    <IconButton onClick={() => setMenuOpen(false)} style={{alignSelf: 'end'}}>
                        <CloseIcon/>
                    </IconButton>
                    <List onClick={() => setMenuOpen(false)}>

                        <ListSubheader>COINEUS.APP</ListSubheader>
                        <ListItem button component={RouterLink} to={'/'}>
                            <ListItemText primary="My Wallet" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={RouterLink} to={'/staking'}>
                            <ListItemText primary="Stake FUSE" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={RouterLink} to={'/sale'}>
                            <ListItemText primary="Projects For Sale" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={RouterLink} to={'/portfolio'}>
                            <ListItemText primary="Fund Holdings" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>

                        <ListSubheader>CNS TOKEN</ListSubheader>
                        <ListItem button component={Link} href={'https://app.voltage.finance/#/swap?inputCurrency=0xc2e299b47398963c618de5b05c6bdecd4cc64022'} target="_blank">
                            <ListItemText primary="Buy" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://www.dextools.io/app/fuse/pair-explorer/0x0f2e60b799f2237633754f8eec7970c125e15d75'} target="_blank">
                            <ListItemText primary="Chart" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://app.voltage.finance/#/bridge'} target="_blank">
                            <ListItemText primary="Bridge" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>

                        <ListSubheader>SOCIAL</ListSubheader>
                        <ListItem button component={Link} href={'https://t.me/coineuschat'} target="_blank">
                            <ListItemText primary="Telegram" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                        <ListItem button component={Link} href={'https://twitter.com/CoineusCrypto'} target="_blank">
                            <ListItemText primary="Twitter" primaryTypographyProps={{style:{fontWeight:700}}}/>
                        </ListItem>
                    </List>
                </Drawer>

            </Router>
        </ThemeProvider>
    )
}

function getChainLogo(id) {
    const size = {width: 16, height: 16}
    const style = {marginRight: 10}
    switch (id) {
        case 1:
            return <Avatar src='https://coineus.app/assets/tokens/eth/logo.png' sx={size} style={style}/>
        case 56:
            return <Avatar src='https://coineus.app/assets/tokens/bnb/logo.png' sx={size} style={style}/>
        case 122:
            return <Avatar src='https://coineus.app/assets/tokens/fuse/logo.png' sx={size} style={style}/>
        case 43114:
            return <Avatar src='https://coineus.app/assets/tokens/avax/logo.png' sx={size} style={style}/>
        default:
            return ''
    }


}